<template>
  <div class="main-div">
    <!-- {{ this.selectedReport}} -->
    <!-- {{this.department}} -->
    <br />
    <b-row>
      <div class="center">
        <h5 class="center">Reports list</h5>
      </div>
    </b-row>
    <br />
    <b-row>
      <b-col cols="8">
        <b-row>
          <div class="table-div">
            <b-container fluid>
              <b-table
                striped
                class="table table-bordered table-sm"
                thead-class="thead-light"
                selectable
                :select-mode="selectMode"
                selected-variant="warning"
                @row-selected="onRowSelected"
                :items="reportItems"
                :fields="fields"
                :current-page="currentPage"
                :per-page="perPage"
                :filter="filter"
                :sort-by.sync="sortBy"
                :sort-desc.sync="sortDesc"
                :sort-direction="sortDirection"
                :hover="true"
              >
                <template v-slot:cell(selected)="{ rowSelected }">
                  <template v-if="rowSelected">
                    <span class="sr-only">Selected</span>
                  </template>
                  <template v-else>
                    <span class="sr-only">Not selected</span>
                  </template>
                </template>
                <!-- <template v-slot:cell(action)="row">
            <b-button v-show="showReportBtn" size="sm" @click="runReport(row.item)">{{
              "Show"
            }}</b-button>
          </template> -->
              </b-table>
              <b-pagination
                v-model="currentPage"
                :total-rows="totalRows"
                :per-page="perPage"
                size="sm"
                class="my-0"
              ></b-pagination>
            </b-container>
          </div>
        </b-row>
      </b-col>
      <b-col cols="4">
        <b-row>
          <b-form-row v-show="dateParameter">
            <b-col cols="4">
              <b-form-group
                label="From Date"
                label-for="rptFromDate"
                label-align="left"
              >
                <b-input-group>
                  <datepicker
                    placeholder="Select Date"
                    v-model="rptFromDate"
                    format="dd/MM/yyyy"
                    input-class="form-control form-control-sm styleLine"
                  ></datepicker>
                </b-input-group>
              </b-form-group>
            </b-col>
            <b-col cols="6">
              <b-form-group
                label="To Date"
                label-for="rptToDate"
                label-align="left"
              >
                <b-input-group>
                  <datepicker
                    placeholder="Select Date"
                    v-model="rptToDate"
                    format="dd/MM/yyyy"
                    input-class="form-control form-control-sm styleLine"
                  ></datepicker>
                </b-input-group>
              </b-form-group>
            </b-col>
          </b-form-row>
          <b-form-row v-show="departmentParameter">
            <!-- <b-col cols="6">
              <b-form-group
                label="Division"
                label-for="divCode"
                label-align="left"
                class="mb-1 textBoxWidth"
              >
                <b-input-group>
                  <b-form-input
                    class="form-control form-control-sm styleLine"
                    style="padding-left: 1px"
                    id="divCode"
                    v-model="divCode"
                  ></b-form-input>
                </b-input-group>
              </b-form-group>
            </b-col> -->
            <b-col cols="6" class="col-left">
          <label for>Department:</label>
        </b-col>
        <b-col cols="6">
            <b-form-group>
              <b-input-group>
                <b-form-input
                  class="col-right form-control form-control-sm required-color"
                  v-model="deptCode"
                  required
                  placeholder="Department code"
                  @change="validateDepartmentCode"
                  v-b-tooltip.hover
                  title="Enter department code"
                ></b-form-input>
                <template v-slot:append>
                  <searchSm
                    :column1Key="searchDept.attr1.name"
                    :column1label="searchDept.attr1.label"
                    :column2Key="searchDept.attr2.name"
                    :column2label="searchDept.attr2.label"
                    :column3Key="searchDept.attr3.name"
                    :column3label="searchDept.attr3.label"
                    :fetchItem="fetchDepartmentCode"
                    @fetchD="fetchDepartmentList"
                    :items="departmentList"
                    :totalRowsT="tRows"
                  />
                </template>
              </b-input-group>
            </b-form-group>
          </b-col>
          </b-form-row>
          <b-form-row v-show="employeeParameter">
            <b-col cols="6">
              <b-form-group
                label="Employee"
                label-for="empCode"
                label-align="left"
                class="mb-1 textBoxWidth"
              >
                <b-input-group>
                  <b-form-input
                    class="form-control form-control-sm styleLine"
                    style="padding-left: 5px"
                    id="empCode"
                    v-model="empCode"
                  ></b-form-input>
                </b-input-group>
              </b-form-group>
            </b-col>
          </b-form-row>
            <b-form-row v-show="payrollHistoryParameter">
            <b-col cols="6">
              <b-form-group
                label="Payroll Month"
                label-for="payrollMonth"
                label-align="left"
                class="mb-1 textBoxWidth"
              >
                <b-input-group>
                  <b-form-input
                    class="form-control form-control-sm styleLine"
                    style="padding-left: 5px"
                    id="payrollMonth"
                    v-model="payrollMonth"
                  ></b-form-input>
                </b-input-group>
              </b-form-group>
            </b-col>
          </b-form-row>
           <b-form-row v-show="payrollHistoryParameter">
              <b-col cols="6">
              <b-form-group
                label="Payroll Year"
                label-for="payrollYear"
                label-align="left"
                class="mb-1 textBoxWidth"
              >
                <b-input-group>
                  <b-form-input
                    class="form-control form-control-sm styleLine"
                    style="padding-left: 5px"
                    id="payrollYear"
                    v-model="payrollYear"
                  ></b-form-input>
                </b-input-group>
              </b-form-group>
            </b-col>
          </b-form-row>
        </b-row>
        <b-row>
            <b-col cols="4">
              <b-button variant="secondary" @click="runReport()">
                {{ "Show Report" }}</b-button
              >
            </b-col>
        </b-row>
      </b-col>
    </b-row>
    <hr />
    <div v-show="isBusy">
      <template>
        <div class="text-center text-danger my-2">
          <b-spinner class="align-middle"></b-spinner>
          <h4><strong>Please wait...</strong></h4>
        </div>
      </template>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
import { mapGetters } from 'vuex'
import Datepicker from 'vuejs-datepicker'
import searchSm from '../components/SearchSm.vue'
const items = []
export default {
  data () {
    return {
      isBusy: false,
      directorateList: [],
      errors: [],
      selectMode: 'single',
      selected: [],
      selectedReport: '',
      rptFromDate: '',
      rptToDate: '',
      reportItems: [],
      dateParameter: false,
      deptCode: '',
      divisionCode: '',
      empCode: 0,
      department: {},
      departmentList: [],
      departmentParameter: false,
      employeeParameter: false,
      payrollHistoryParameter: false,
      payrollYear: 0,
      tRows: 0,
      searchDept: {
        attr1: { name: 'id', label: 'Department Code' },
        attr2: { name: 'departmentName', label: 'Department Name' },
        attr3: { name: 'divisionName', label: 'Division Name' }
      },
      fields: [
        {
          key: 'reportId',
          label: 'Report Id',
          sortable: true,
          sortDirection: 'desc',
          class: 'text-left'
        },
        // {
        //   key: 'reportName',
        //   label: 'Report name',
        //   sortable: true,
        //   sortDirection: 'desc',
        //   class: 'text-left'
        // },
        {
          key: 'reportDescription',
          label: 'Report Description',
          sortable: true,
          sortDirection: 'desc',
          class: 'text-left',
          tdClass: 'col-width-300'
        }
        // ,
        // { key: 'action', label: 'Show Report' }
      ],
      currentPage: 1,
      perPage: 10,
      totalRows: items.length,
      pageOptions: [5, 10, 15],
      sortBy: null,
      sortDesc: false,
      sortDirection: 'asc',
      filter: null
    }
  },
  computed: {
    ...mapGetters([])
  },
  components: {
    Datepicker,
    searchSm
    // baseList
  },
  mounted () {
    this.getReportList()
  },
  methods: {
    validateDepartmentCode () {
      this.fetchDepartmentCode(this.deptCode)
    },
    async getReportList () {
      const url = process.env.VUE_APP_API_URL + '/reports'
      await axios
        .get(url, {
          headers: {
            'Content-Type': 'application/json'
          }
        })
        .then((response) => {
          // console.log('=== No Error returned ====')
          this.reportItems = response.data
          this.totalRows = this.reportItems.length
        })
        .catch((error) => {
          console.log('Error ' + error.response)
        })
    },
    onFiltered (filteredItems) {
      this.totalRows = filteredItems.length
      this.currentPage = 1
    },
    getCurrentPage: function (x) {},
    reSetParameterDisplay () {
      this.dateParameter = false
      this.departmentParameter = false
      this.employeeParameter = false
    },
    fetchDepartmentList () {
      const url = process.env.VUE_APP_API_URL + '/departmentView'
      fetch(url)
        .then((res) => res.json())
        .then((data) => {
          this.departmentList = data
          this.tRows = this.departmentList.length
        })
        .catch((err) => console.error(err))
    },
    fetchDepartmentCode (x) {
      this.errors = []
      const url = process.env.VUE_APP_API_URL + `/department/${x}`
      fetch(url)
        .then((res) => res.json())
        .then((data) => {
          this.department = data
          this.deptCode = this.department.id
          this.divisionCode = this.department.divisionCode
        })
        .catch((err) => {
          alert(err)
          // this.errors.push('Invalid Department code')
          // this.showErrorMessage('Invalid Department code')
        })
    },
    onRowSelected (x) {
      this.selectedReport = x[0].reportName
      if (typeof x[0] !== 'undefined') {
        switch (this.selectedReport) {
          case 'prs1':
            this.reSetParameterDisplay()
            break
          case 'prs1_department':
            this.reSetParameterDisplay()
            this.departmentParameter = true
            break
          case 'prs1_department_analysis':
            this.reSetParameterDisplay()
            break
          case 'llo':
            this.reSetParameterDisplay()
            this.dateParameter = true
            break
          case 'pslip-i':
            this.reSetParameterDisplay()
            this.employeeParameter = true
            break
          case 'pslip-all':
            this.reSetParameterDisplay()
            break
          case 'pslipHistory-i':
            this.reSetParameterDisplay()
            this.employeeParameter = true
            this.payrollHistoryParameter = true
            break
          case 'emp_salary_increment':
            this.reSetParameterDisplay()
            break
          case 'emp_salary_increment_i':
            this.reSetParameterDisplay()
            this.employeeParameter = true
            break
          case 'emp_in_service':
            this.reSetParameterDisplay()
            break
          case 'emp_joined_currentyear':
            this.reSetParameterDisplay()
            break
          case 'emp_leftservice':
            this.reSetParameterDisplay()
            break
          case 'emp_leftservice_currentyear':
            this.reSetParameterDisplay()
            break
          default:
            break
        }
      }
    },
    checkParameter (x) {
      this.errors = []
      switch (x) {
        case 'XXCAS003':
          if (!this.rptTenderNo) {
            this.errors.push('Tender No')
          }
          break
        case 'XXCAS004':
          break
        default:
          break
      }
      if (!this.errors.length) {
        return true
      } else {
        this.errors.unshift(
          'Report parameters are required, please enter following '
        )
      }
    },
    formatDate (d) {
      var n = d.getMonth() + 1
      return d.getFullYear() + '-' + n + '-' + d.getDate()
    },
    runReport: function () {
      this.isBusy = !this.isBusy
      const x = this.selectedReport
      console.log('selected report:' + x)
      let fmDate = ''
      let toDate = ''
      // this.checkParameter(x)
      if (!this.errors.length) {
        switch (x) {
          case 'prs1':
            window.open(
              process.env.VUE_APP_REPORT_URL +
                `${x}&rs:Command=Render&rs:format=PDF`
            )
            break
          case 'prs1_department_analysis':
            window.open(
              process.env.VUE_APP_REPORT_URL +
                `${x}&rs:Command=Render&rs:format=PDF`
            )
            break
          case 'prs1_department':
            window.open(
              process.env.VUE_APP_REPORT_URL +
                `${x}&rs:Command=Render&rs:format=PDF&divisionCode=${this.divisionCode}&deptcode=${this.deptCode}`
            )
            break
          case 'llo':
            window.open(
              process.env.VUE_APP_REPORT_URL +
                `${x}&rs:Command=Render&rs:format=PDF`
            )
            break
          case 'llo-new':
            window.open(
              process.env.VUE_APP_REPORT_URL +
                `${x}&rs:Command=Render&rs:format=PDF`
            )
            break
          case 'pslip-i':
            window.open(
              process.env.VUE_APP_REPORT_URL +
                `${x}&rs:Command=Render&rs:format=PDF&empCode=${this.empCode}`
            )
            break
          case 'pslip-all':
            window.open(
              process.env.VUE_APP_REPORT_URL +
                `${x}&rs:Command=Render&rs:format=PDF`
            )
            break
          case 'pslipHistory-i':
            window.open(
              process.env.VUE_APP_REPORT_URL +
                `${x}&rs:Command=Render&rs:format=PDF&empCode=${this.empCode}&trnMonth=${this.payrollMonth}&trnYear=${this.payrollYear}`
            )
            break
          case 'emp_salary_increment':
            // P_DIR_CODE todate, from date
            fmDate = this.formatDate(this.rptFromDate)
            toDate = this.formatDate(this.rptToDate)
            window.open(
              process.env.VUE_APP_REPORT_URL +
                `${x}&rs:Command=Render&rs:format=PDF&p_from_dt=${fmDate}&p_to_dt=${toDate}`
            )
            break
          case 'emp_in_service':
            window.open(
              process.env.VUE_APP_REPORT_URL +
                `${x}&rs:Command=Render&rs:format=PDF`
            )
            break
          case 'emp_joined_currentyear':
            window.open(
              process.env.VUE_APP_REPORT_URL +
                `${x}&rs:Command=Render&rs:format=PDF`
            )
            break
          case 'emp_leftservice':
            window.open(
              process.env.VUE_APP_REPORT_URL +
                `${x}&rs:Command=Render&rs:format=PDF`
            )
            break
          case 'emp_leftservice_currentyear':
            window.open(
              process.env.VUE_APP_REPORT_URL +
                `${x}&rs:Command=Render&rs:format=PDF`
            )
            break
          case 'emp_salary_increment_i':
            window.open(
              process.env.VUE_APP_REPORT_URL +
                `${x}&rs:Command=Render&rs:format=PDF&empCode=${this.empCode}`
            )
            break
        }
      } else {
        alert(this.errors)
      }
      this.isBusy = !this.isBusy
    }
  }
}
</script>

<style scoped>
.main-div {
  width: 95%;
  margin: auto;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  font-size: medium;
}
.table-div {
  width: 70%;
  margin: auto;
  font-size: medium;
}
.col-left {
  text-align: left;
}
.col-right {
  text-align: right;
}
.input-required {
  background-color: rgb(245, 242, 212);
}

#b-col {
  text-align: left;
}

.textBoxWidth {
  width: 160px;
}
.cellColor {
  background-color: #d9dcdf;
}
.calculated-value {
  background-color: #d9dcdf;
}
.sub-head {
  background-color: #d9dcdf;
}
.group1-color {
  background-color: rgb(242, 242, 242);
}
.group2-color {
  background-color: rgb(230, 230, 230);
}
p {
  margin-bottom: 0px;
}
.spacer {
  margin-top: 5px;
  margin-bottom: 5px;
}
.col-width-300 {
  width: 300px;
}
.center {
  margin-left: auto;
  margin-right: auto;
}
</style>
